@import url(https://fonts.googleapis.com/css2?family=Gugi&display=swap);

:root {
	--background: #ffcf6f;
	--black: black;
	--green: #22bc7d;
	--blue: #0067bb;
	--red: #ff4334;
}

.container {
	display: flex;
	align-items: center;
	justify-content: space-around;
	height: 100vh;
	min-width: 580px;
}

.container__bgImage,
.container__column {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
	padding: 0 2rem;
}

.container__link {
	display: flex;
	flex-direction: column;
}

.container__bgImage {
	justify-content: flex-end;
}

.home__link {
	margin: 3rem 0 0 0;
	color: black;
	color: var(--black);
	font-size: 2em;
	font-family: 'Gugi', cursive;
	text-align: center;
	text-decoration: none;
	min-width: 10rem;
}

.logo__whiteBox {
	background-color: white;
	width: 20rem;
	height: 15%;
}

.image__logo {
	width: 20rem;
}

.bg__blueBox,
.bg__redBox {
	width: 19rem;
	height: 15%;
}

.bg__blueBox {
	background-color: #0067bb;
	background-color: var(--blue);
}

.bg__redBox {
	background-color: #ff4334;
	background-color: var(--red);
}

.bg__cat,
.bg__dog {
	width: 20rem;
}

.container__bgImage__small {
	display: none;
}

@media screen and (max-width: 1200px) {
	.logo__whiteBox,
	.container__bgImage {
		display: none;
	}

	.container__column {
		justify-content: space-between;
	}

	.container__bgImage__small {
		display: inline;
		display: initial;
		/* div 사이의 간격 없애기 */
		font-size: 0;
	}

	.image__logo {
		width: 15rem;
	}
	.bg__cat,
	.bg__dog {
		width: 10rem;
		margin: 4rem 2rem 0 2rem;
	}
}
.navigation {
	position: absolute;
	top: 0;
	left: 0;
	background-color: var(--green);
	width: 9rem;
	height: 3rem;
	border-radius: 0 0 4rem 0;
	padding: 1.5rem 0 0 2rem;
	z-index: 2;
}

.navigation a {
	color: white;
	text-decoration: none;
	font-size: 1.3rem;
}
.container__beforeStart {
	width: 100vw;
	height: 100vh;
}

.container__choices {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translate(-50%);
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.question {
	font-size: 1.4rem;
	margin: 1.5rem 0 0 0;
}

.question__comment {
	font-size: 1rem;
	margin: 0 0 .5rem;
	color: #00000066;
}

.container__choice {
	display: flex;
	flex-direction: row;
}

.choice__mark,
.choice {
	display: flex;
	flex-direction: column;
	width: 12rem;
	height: 6rem;
	background: linear-gradient(160deg, #ffffff30 60%, #ffffff55 1%, #ffffff20);
	border: .2rem solid #ffffff55;
	align-items: center;
	justify-content: center;
	border-radius: 1rem;
	margin-right: 1rem;
	font-size: 1.5rem;
	color: white;
}

.choice__mark {
	background-color: var(--green);
	color: white;
}

.choice__mark p,
.choice p {
	margin: 0;
}

.choice__cat,
.choice__dog {
	width: 4rem;
}

/* ==== 게임 시작 버튼 ==== */

.button__gamestart {
	width: 100%;
	height: 3rem;
	margin: 2rem 0;
}

.button__gamestart a {
	text-decoration: none;
}

.button__gamestart p {
	background-color: var(--green);
	padding : 2rem;
	text-align: center;
	color: white;
	border-radius: 4rem;
	font-size: 1.5rem;
}
.pos_column,
.tiles_column {
	position: absolute;
	top: 50%;
	left: 50%;
	/* 기준점을 x축 중앙으로 옮기기 */
	transform: translate(-50%, -50%);
	/* div들 사이에 공백 없애기 위해. */
	font-size: 0;
}

.pos_row,
.tiles_row {
	display: flex;
}

.pos_row canvas {
	/* Position 가 약간 안 맞는 걸 맞추기 위해 */
	transform: translate(-1px);
}

.container__mapbg {
	min-width: 450px;
	min-height: 450px;
	width: 30rem;
	height: 30rem;
	background-color: white;
	border: .3rem solid black;
	border-radius: 2rem;
}

.container__marker {
	position: absolute;
	/* Marker 가 약간 안 맞는 걸 맞추기 위해 */
	transform: translate(-1px);
}

.dice {
	min-width: 110px;
	min-height: 96px;
	width: 10rem;
	position: relative;
	left: 0;
	top: 0;
	/* background-color: blue; */
}

.dice canvas {
	/* canvas 의 기준점이 중앙으로 오도록 한다 */
	/* absolute 는 static 이 아닌 부모를 기준으로 한다 */
	position: absolute;
	top: 50%;
	left: 50%;
	/* canvas 의 기준점을 중앙으로 옮기기 */
	transform: translate(-50%, -50%);
}

.dice p {
	/* 중아으로 오도록 */
	position: absolute;
	top: 58%;
	left: 50%;
	transform: translate(-50%, -50%);
	/* default margin 지우기 */
	margin: 0;

	font-size: 2rem;
	text-align: center;
}
.container__game {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-around;
	height: 100vh;
	min-width: 520px;
}

.container__top {
	display: flex;
	min-height: 96px;
	align-items:center;
	/* navigation 과 겹치지 않기 위해 */
	margin-top: 3rem;
}

.container__top div {
	color: black;
	font-size: 1.4rem;
}

.container__dice {
	display: flex;
	justify-content: space-around;
}

.gameFocusInput {
	opacity: 0;
}

.container__map {
	/* absolute 인 자식컴포넌트들의 기준이 되기 위해. */
	position: relative;
}

/* ========== 배경 이미지 ========== */

.image__cat,
.image__dog {
	width: 15rem;
}

.logo__blueBox,
.logo__redBox {
	width: 14.25rem;
	height: 20%;
}

.logo__blueBox {
	background-color: var(--blue);
}

.logo__redBox {
	background-color: var(--red);
}

.icon__cat,
.icon__dog {
	width: 4rem;
	padding: 0 2rem;
}

/* ========== 키보드 키 사용 설명 부분 ========== */

.container__button {
	display: flex;
	padding-top: 3rem;
	/* container__score__bottom 과 겹쳐지지 않게 하기 위해 */
	padding-bottom: 5rem;
}

.container__button button {
	font-size: 1rem;
}

.container__spacebar {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.button__spacebar {
	width: 15rem;
	height: 5rem;
	background: linear-gradient(160deg, #ffffff30 60%, #ffffff55 1%, #ffffff20);
	border: .1rem solid #ffffff55;
	border-radius: 3rem;
	color: white;
}

.container__arrow {
	display: flex;
	flex-direction: column;
	align-items: center;
	/* 스페이스바 버튼이랑 간격 */
	padding-left: 3rem;
}

.container__arrowKey {
	display: flex;
	flex-direction: column;
	width: 12.8rem;
	height: 5rem;
	align-items: center;
	justify-content: space-between;
}

.container__arrow p,
.container__spacebar p {
	color: white;
	font-size: 1.3rem;
}

.container__arrowKey div {
	display: flex;
	width: 100%;
	justify-content: space-between;
}

.button__arrow {
	width: 4rem;
	height: 2.2rem;
	background: linear-gradient(160deg, #ffffff30 60%, #ffffff55 1%, #ffffff20);
	border: .1rem solid #ffffff55;
	border-radius: 1.5rem;
	color: white;
}

/* ==========점수판 관련 ========== */

.score__bg,
.score__highlight {
	display: flex;
	width: 6rem;
	height: 6rem;
	align-items: center;
	justify-content: center;
	transform: rotate(45deg);
	border-radius: 5rem 5rem 0 5rem;
	/* 회전으로 인해 이미지와 살짝 겹치는 것을 피하기 위해 */
	margin: 1rem 0;
}

.score__bg {
	background-color: white;
	color: black;
	font-size: 2.5rem;
}

.score__highlight {
	background-color: var(--green);
	color: white;
	font-size: 5rem;
	/* 차례인 쪽 크기 커지기 */
	width: 10rem;
	height: 10rem;
}

.score__bg div,
.score__highlight div {
	transform: rotate(-45deg);
}

.container__score__bottom {
	display: none;
}

@media screen and (max-width: 1200px) {
	.container__score__bottom {
		position: fixed;
		bottom: 0;
		display: flex;
		width: 100vw;
		height: 4rem;
		background-color: white;
		align-items: center;
		justify-content: space-around;
		z-index: 2;
		min-width: 520px;
	}

	.score__bg,
	.score__highlight {
		display: flex;
		width: 50%;
		height: 100%;
		font-size: 2rem;
		border-radius: 0;
		margin: 0;
		transform: rotate(0);
	}

	.score__bg div,
	.score__highlight div {
		transform: rotate(0);
	}
}
body {
	margin: 0;
	background-color: var(--background);
}
